export default {
  data() {
    return {
      city: "成都",
      date: "",
      showPicker: false,
      showDatePicker: false,
    };
  },
  created() {
    this.initDate();
  },
  methods: {
    onMonthConfirm(date) {
      this.showDatePicker = false;
      this.initDate(date);
    },
    onCityConfirm(city) {
      this.city = city;
      this.showPicker = false;
    },
    onCityOpen() {
      this.showPicker = true;
    },
    onDateOpen() {
      this.showDatePicker = true;
    },
    initDate(date) {
      date = date || new Date();
      this.date = date;
    },
  },
};
